<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>院部管理</el-breadcrumb-item>
      <el-breadcrumb-item>教学大纲列表</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!-- 搜索 -->

      <el-row :gutter="10" class="el-row">
        <el-col :span="1.5">
          <el-button
            type="primary"
            icon="el-icon-plus"
              v-if="is_authority.is_user_add"
            size="mini"
            @click="open = true"
            >新增</el-button
          >
          
        </el-col>
        
        <el-col :span="1.5">
          <el-button
            type="success"
            icon="el-icon-edit"
            size="mini"
            :disabled="single"
            @click="editUser"
            v-if="is_authority.is_user_upt"
            >修改</el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            :disabled="multiple"
            v-if="is_authority.is_user_del"
            @click="del"
            >删除</el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button type="info" icon="el-icon-upload2" size="mini"
            >导入</el-button
          >
          <!-- @click="handleImport" -->
          <!-- v-hasPermi="['system:user:import']" -->
        </el-col>
        <el-col :span="1.5">
          <el-button type="warning" icon="el-icon-download" size="mini"
            >导出</el-button
          >
          <!-- @click="handleExport"
                      v-hasPermi="['system:user:export']" -->
        </el-col>
        <!-- <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar> -->
      </el-row>

      <!-- 数据 -->
      <el-table
        :data="List"
        border
        stripe
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="50" align="center" />

        <el-table-column
          align="center"
          prop="sylName"
          label="大纲课程名称"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="cname"
          label="开设专业"
        ></el-table-column>
        <el-table-column align="center" label="计划大纲预览">
          <template slot-scope="scope">
            <a :href="scope.row.sylPlanPdf">
              <img
                width="50px"
                height="50px"
                src="https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=206184139,2148631054&fm=11&gp=0.jpg"
                :alt="scope.row.sylPlanPdf"
              />
            </a>
          </template>
        </el-table-column>

        <el-table-column align="center" label="计划大纲下载">
          <template slot-scope="scope">
            <a :href="scope.row.sylPlanWord">
              <img
                width="50px"
                height="50px"
                src="http://cdn.u1.huluxia.com/g4/M02/49/0C/rBAAdl_PeUqASp0DAAAZmCzn4uE431.png"
                :alt="scope.row.sylPlanWord"
              />
            </a>
          </template>
        </el-table-column>
        <el-table-column align="center" label="大纲预览">
          <template slot-scope="scope">
            <a :href="scope.row.sylRealPdf">
              <img
                width="50px"
                height="50px"
                src="https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=206184139,2148631054&fm=11&gp=0.jpg"
                :alt="scope.row.sylRealPdf"
              />
            </a>
          </template>
        </el-table-column>
        <el-table-column align="center" label="大纲下载">
          <template slot-scope="scope">
            <a :href="scope.row.sylRealWord">
              <img
                width="50px"
                height="50px"
                src="http://cdn.u1.huluxia.com/g4/M02/49/0C/rBAAdl_PeUqASp0DAAAZmCzn4uE431.png"
                :alt="scope.row.sylRealWord"
              />
            </a>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="sylDemo"
          label="简介"
          show-overflow-tooltip
        ></el-table-column>

        <el-table-column label="操作" width="120px" align="center">
          <template slot-scope="scope">
            <!-- 修改 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="编辑"
              v-if="is_authority.is_user_upt"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editUser(scope.row)"
              ></el-button>
            </el-tooltip>
            <!-- 删除 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="删除"
              v-if="is_authority.is_user_del"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page.current"
        :page-sizes="[5, 10, 20, 30, 40]"
        :page-size="page.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
      ></el-pagination>
      <!-- 对话框 :before-close="handleClose" 在对话框关闭之前执行-->
      <!-- 标题 -->
      <el-dialog
        title="编辑"
        :visible.sync="open"
        width="50%"
        @close="closeInit"
      >
        <!-- 内容主体 -->

        <el-form
          :model="Form"
          :rules="rules"
          ref="formRef"
          label-width="110px"
          class="demo-ruleForm"
        >
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item
                label="大纲课程名称"
                prop="sylName"
                label-width="110px"
              >
                <el-input
                  v-model="Form.sylName"
                  placeholder="请输入内容"
                ></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="课程" prop="cid">
                <el-select
                  v-model="Form.cid"
                  placeholder="请输入内容"
                  filterable
                >
                  <el-option
                    v-for="item in cidOptions"
                    :key="item.id"
                    :label="item.cname"
                    :value="item.cid"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="计划大纲上传" prop="sylPlanPdf">
                <el-upload
                  :file-list="fileList"
                  class="upload-demo"
                  action="http://www.dayanhulian.com:8001/matter/fileoss/uploadOssFile"
                  multiple
                  :headers="config"
                  :limit="3"
                  :on-success="saveSylPlanPdf"
                  :before-upload="beforeUpload"
                  accept=".pdf,.doc,docx"
                >
                  <el-button icon="el-icon-upload2" size="small" type="primary"
                    >点击上传</el-button
                  >
                  <span slot="tip" class="el-uploaddTip"
                    >只能上传pdf/word文件</span
                  >
                </el-upload>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="大纲上传" prop="sylRealWord">
                <el-upload
                  :file-list="fileList"
                  class="upload-demo"
                  action="http://www.dayanhulian.com:8001/matter/fileoss/uploadOssFile"
                  multiple
                  :headers="config"
                  :limit="3"
                  :on-success="saveSylRealWord"
                  :before-upload="beforeUpload"
                  accept=".pdf,.doc,docx"
                >
                  <el-button icon="el-icon-upload2" size="small" type="primary"
                    >点击上传</el-button
                  >
                  <span slot="tip" class="el-uploaddTip"
                    >只能上传pdf/word文件</span
                  >
                </el-upload>
              </el-form-item>
            </el-col>
          </el-row>

          <el-form-item label="简介" show-overflow-tooltip prop="sylDemo">
            <el-input
              v-model="Form.sylDemo"
              placeholder="请输入内容"
            ></el-input>
          </el-form-item>
        </el-form>
        <!-- 按钮 -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="open = false">取 消</el-button>
          <el-button type="primary" @click="submitForm">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      token: window.sessionStorage.getItem('token'),
      fileList: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      List: [],
      queryVo: {
        page: {
          current: 1,
          size: 5
        }
      },
      page: {
        current: 1,
        size: 5,
        total: 0
      },
      ids: [],
      rules: {
        sylName: [{ required: true, message: '不能为空', trigger: 'blur' }],
        sylPlanPdf: [{ required: true, message: '不能为空', trigger: 'blur' }],
        sylRealWord: [{ required: true, message: '不能为空', trigger: 'blur' }],
        cid: [{ required: true, message: '不能为空', trigger: 'blur' }]
      },
      // subOptions: [],
      cidOptions: [],
      Form: {},
      open: false
    }
  },
  created() {
    this.isAuthority()
    this.queryPage()
    this.getTreeselect()
  },
  computed: {
    ...mapState(['btus']),
    config() {
      return { Token: this.token }
    }
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    beforeUpload(file) {
      console.log(file.type)

      const isjpg = file.type === 'application/msword' || 'application/pdf'
      if (!isjpg) {
        this.$message.error('请上传 PDF或者WORD 格式!')
      }
      return isjpg
    },
    async saveSylPlanPdf(response, file, fileList) {
      this.Form.sylPlanPdf = response.data.url
      this.Form.sylPlanWord = response.data.url
      console.log(response)
    },

    async saveSylRealWord(response, file, fileList) {
      this.Form.sylRealPdf = response.data.url
      this.Form.sylRealWord = response.data.url
      console.log(response)
    },
    async getTreeselect() {
      const { data: res } = await this.$http.post(
        '/couresInfo/queryCouresInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      const a = res.data.dataList.records
      this.cidOptions = JSON.parse(JSON.stringify(a).replace(/id/g, 'cid'))
      if (res.code != 20000) return this.$message.error(res.message)
      // console.log(this.cidOptions)
      // console.log(JSON.stringify(this.cidOptions).replace(/id/g,'cid'))
    },

    unique(arr) {
      const res = new Map()
      return arr.filter(arr => !res.has(arr.id) && res.set(arr.id, 1))
    },
    submitForm() {
      this.$refs.formRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post(
            '/syllabusInfo/saveOrUpdateSyllabusInfo',
            this.Form
          )
          if (res.code != 20000) return this.$message.error(res.message)
          this.open = false
          this.queryPage()
          return this.$message.success(res.message)
        }
      })
    },

    closeInit() {
      // console.log(this.Form);
      this.Form = {}
      this.$refs.formRef.resetFields()
    },

    async editUser(row) {
      this.open = true
      const id = row.id || this.ids
      console.log(id)
      const { data: res } = await this.$http.get(
        '/syllabusInfo/getByIdSyllabusInfo/' + id
      )
      if (res.code != 20000) return this.$message.error(res.message)

      this.Form = res.data.entity
      this.Form.cid = this.Form.cid + ''
      console.log(this.Form)
    },
    del(row) {
      if (row.id) {
        var a = row.id.split(',')
      }
      const userid = a || this.ids
      console.log(userid)
      this.$confirm('确认要删除吗?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await this.$http.delete('/syllabusInfo/removeByIdSyllabusInfos', {
            data: userid
          })
          // console.log(res);
          this.queryPage()
          this.List = []
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        })
    },
    async queryPage() {
      const { data: res } = await this.$http.post(
        'syllabusInfo/querySyllabusInfoList',
        this.queryVo
      )
      console.log(res.data.dataList.records)
      if (res.code != 20000) return this.$message.error(res.message)
      this.List = res.data.dataList.records
      this.page.total = res.data.dataList.total
      this.page.current = res.data.dataList.current
      this.page.size = res.data.dataList.size
      // console.log(this.List)
    },
    //选择框
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length != 1
      this.multiple = !selection.length
    },
    //更新状态
    async uptStatus(status) {
      const { data: res } = await this.$http.put(
        `/jxyxUser/users/${status.id}/state/${status.mgState}`
      )
      if (res.code != 20000) {
        status.mgState = !status.mgState
        return this.$message.error(res.message)
      }
      return this.$message.success(res.message)
    },
    // 监听每页展示的条数
    handleSizeChange(newSize) {
      this.queryVo.page.size = newSize
      this.queryPage()
    },
    //监听页码
    handleCurrentChange(newCurrent) {
      this.queryVo.page.current = newCurrent
      this.queryPage()
    }
  }
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
.el-row {
  display: flex;
  align-items: center;
}
.el-pagination {
  margin-top: 15px;
}
</style>
